document.addEventListener('DOMContentLoaded', () => {
    eventListeners();
    darkMode();
    eliminarMensajes();
});

const miStorage = window.localStorage;
const prefiereDarkMode = window.matchMedia('(prefers-color-scheme: dark)');

function eventListeners() {
    const mobileMenu = document.querySelector('.mobile-menu');

    mobileMenu.addEventListener('click', navegacionResponsive);

    //Muestra campos condicionales
    const metodoContacto = document.querySelectorAll('input[name="contacto[contacto]"]');

    metodoContacto.forEach(input => input.addEventListener('click', mostrarMetodosContacto))

}

function navegacionResponsive() {
    const navegacion = document.querySelector('.nav-header');
    
    navegacion.classList.toggle('mostrar');
}

function darkMode() {
    const botonDarkMode = document.querySelector('.dark-mode-boton');

    if (miStorage.getItem('oscuro') == null) {
        activarDarkMode();
    } else {
        if (miStorage.getItem('oscuro') == 1) { 
            document.body.classList.add('dark-mode');
        } else {                                     
            document.body.classList.remove('dark-mode');
        }
    }


    prefiereDarkMode.addEventListener('change', () => {
        activarDarkMode();
    });

    botonDarkMode.addEventListener('click', () => {
        if (document.body.classList.contains('dark-mode')) {
            miStorage.setItem('oscuro', 0);
            document.body.classList.remove('dark-mode');

        } else {
            miStorage.setItem('oscuro', 1);
            document.body.classList.add('dark-mode');
        }
    });
}

function activarDarkMode() {
    if (prefiereDarkMode.matches) {
        miStorage.setItem('oscuro', 1);
        document.body.classList.add('dark-mode');

    } else {
        miStorage.setItem('oscuro', 0);
        document.body.classList.remove('dark-mode');
    }
}

function eliminarMensajes() {
    const alertas = document.querySelectorAll('.alerta');
    if (alertas) {
        setTimeout(() => {
            alertas.forEach(alerta => {
                const padre = alerta.parentElement;
                padre.removeChild(alerta);
            });
        }, 3500);
    }
}

function mostrarMetodosContacto(e) {
    const contactoDiv = document.querySelector('#contacto');

    if(e.target.value === 'telefono') {
        contactoDiv.innerHTML = `
            <label for="telefono">Número de Teléfono</label>
            <input type="tel" id="telefono" maxlength="11" name="contacto[telefono]" placeholder="Tu Teléfono">

            <p>Elija la fecha y hora</p>

            <label for="fecha">Fecha</label>
            <input type="date" id="fecha" name="contacto[fecha]">

            <label for="hora">Hora</label>
            <input type="time" id="hora" name="contacto[hora]" min="09:00" max="18:00">
        `
    } else {
        contactoDiv.innerHTML = `
            <label for="email">E-mail</label>
            <input type="email" id="email" name="contacto[email]" placeholder="Tu Email" required>
        `
    }
}